.buscar{
    width: 170px;
    height: 24px;
    background: #FFFFFF;
    border: 1px solid #922ebd;
    box-sizing: border-box;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    font-family: 'Quicksand', sans-serif;
    color: #d6d6d6;

}

 
  